'use client'

import { useState, useEffect } from 'react'
import { useUser } from '@/context/UserContext'
import { useRouter } from 'next/navigation'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Badge } from '@/components/ui/badge'
import { Alert, AlertDescription } from '@/components/ui/alert'

interface User {
  id: string
  name: string
  email: string
  role: string
  status: 'active' | 'suspended'
  joinDate: string
}

interface Transaction {
  id: string
  buyer: string
  seller: string
  item: string
  amount: string
  type: 'purchase' | 'rental'
  status: 'completed' | 'pending' | 'disputed'
  date: string
}

interface ReportedItem {
  id: string
  itemName: string
  seller: string
  reporter: string
  reason: string
  status: 'pending' | 'resolved' | 'dismissed'
  date: string
}

export default function AdminDashboard() {
  const { user, isAuthenticated } = useUser()
  const router = useRouter()
  const [users, setUsers] = useState<User[]>([])
  const [transactions, setTransactions] = useState<Transaction[]>([])
  const [reportedItems, setReportedItems] = useState<ReportedItem[]>([])

  useEffect(() => {
    if (!isAuthenticated || user?.role !== 'admin') {
      router.push('/login')
      return
    }

    // Load demo data
    setUsers([
      { id: '1', name: 'John Seller', email: 'seller@thrifthub.com', role: 'seller', status: 'active', joinDate: '2024-01-15' },
      { id: '2', name: 'Jane Buyer', email: 'buyer@thrifthub.com', role: 'buyer', status: 'active', joinDate: '2024-01-20' },
      { id: '3', name: 'Mike Trader', email: 'mike@example.com', role: 'buyer', status: 'suspended', joinDate: '2024-02-01' },
      { id: '4', name: 'Sarah Store', email: 'sarah@example.com', role: 'seller', status: 'active', joinDate: '2024-02-10' },
    ])

    setTransactions([
      { id: '1', buyer: 'Jane Buyer', seller: 'John Seller', item: 'Vintage Leather Jacket', amount: '$45', type: 'purchase', status: 'completed', date: '2024-03-01' },
      { id: '2', buyer: 'Mike Trader', seller: 'Sarah Store', item: 'MacBook Pro 2019', amount: '$800', type: 'purchase', status: 'disputed', date: '2024-03-02' },
      { id: '3', buyer: 'Jane Buyer', seller: 'John Seller', item: 'Canon DSLR Camera', amount: '$50/day', type: 'rental', status: 'pending', date: '2024-03-03' },
    ])

    setReportedItems([
      { id: '1', itemName: 'Fake Designer Bag', seller: 'Mike Trader', reporter: 'Jane Buyer', reason: 'Counterfeit item', status: 'pending', date: '2024-03-01' },
      { id: '2', itemName: 'Broken Phone', seller: 'Sarah Store', reporter: 'John Seller', reason: 'Item not as described', status: 'resolved', date: '2024-02-28' },
    ])
  }, [isAuthenticated, user, router])

  const handleUserAction = (userId: string, action: 'suspend' | 'activate') => {
    setUsers(users.map(u => 
      u.id === userId 
        ? { ...u, status: action === 'suspend' ? 'suspended' : 'active' }
        : u
    ))
  }

  const handleReportAction = (reportId: string, action: 'resolve' | 'dismiss') => {
    setReportedItems(reportedItems.map(item => 
      item.id === reportId 
        ? { ...item, status: action === 'resolve' ? 'resolved' : 'dismissed' }
        : item
    ))
  }

  if (!isAuthenticated || user?.role !== 'admin') {
    return (
      <Alert>
        <AlertDescription>
          Access denied. Admin privileges required.
        </AlertDescription>
      </Alert>
    )
  }

  const stats = {
    totalUsers: users.length,
    activeUsers: users.filter(u => u.status === 'active').length,
    totalTransactions: transactions.length,
    pendingReports: reportedItems.filter(r => r.status === 'pending').length
  }

  return (
    <div className="space-y-8">
      <div>
        <h1 className="text-3xl font-bold text-gray-900">Admin Dashboard</h1>
        <p className="text-gray-600 mt-2">Manage users, transactions, and platform content</p>
      </div>

      {/* Stats Overview */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Total Users</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.totalUsers}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Active Users</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-green-600">{stats.activeUsers}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Total Transactions</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.totalTransactions}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Pending Reports</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-red-600">{stats.pendingReports}</div>
          </CardContent>
        </Card>
      </div>

      {/* Main Content Tabs */}
      <Tabs defaultValue="users" className="space-y-6">
        <TabsList>
          <TabsTrigger value="users">User Management</TabsTrigger>
          <TabsTrigger value="transactions">Transactions</TabsTrigger>
          <TabsTrigger value="reports">Content Moderation</TabsTrigger>
          <TabsTrigger value="analytics">Analytics</TabsTrigger>
        </TabsList>

        <TabsContent value="users">
          <Card>
            <CardHeader>
              <CardTitle>User Management</CardTitle>
              <CardDescription>Manage user accounts and permissions</CardDescription>
            </CardHeader>
            <CardContent>
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Name</TableHead>
                    <TableHead>Email</TableHead>
                    <TableHead>Role</TableHead>
                    <TableHead>Status</TableHead>
                    <TableHead>Join Date</TableHead>
                    <TableHead>Actions</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {users.map((user) => (
                    <TableRow key={user.id}>
                      <TableCell className="font-medium">{user.name}</TableCell>
                      <TableCell>{user.email}</TableCell>
                      <TableCell>
                        <Badge variant="outline" className="capitalize">
                          {user.role}
                        </Badge>
                      </TableCell>
                      <TableCell>
                        <Badge variant={user.status === 'active' ? 'default' : 'destructive'}>
                          {user.status}
                        </Badge>
                      </TableCell>
                      <TableCell>{user.joinDate}</TableCell>
                      <TableCell>
                        <div className="flex space-x-2">
                          {user.status === 'active' ? (
                            <Button 
                              size="sm" 
                              variant="destructive"
                              onClick={() => handleUserAction(user.id, 'suspend')}
                            >
                              Suspend
                            </Button>
                          ) : (
                            <Button 
                              size="sm" 
                              variant="default"
                              onClick={() => handleUserAction(user.id, 'activate')}
                            >
                              Activate
                            </Button>
                          )}
                        </div>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="transactions">
          <Card>
            <CardHeader>
              <CardTitle>Transaction Monitoring</CardTitle>
              <CardDescription>Monitor and manage platform transactions</CardDescription>
            </CardHeader>
            <CardContent>
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Transaction ID</TableHead>
                    <TableHead>Buyer</TableHead>
                    <TableHead>Seller</TableHead>
                    <TableHead>Item</TableHead>
                    <TableHead>Amount</TableHead>
                    <TableHead>Type</TableHead>
                    <TableHead>Status</TableHead>
                    <TableHead>Date</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {transactions.map((transaction) => (
                    <TableRow key={transaction.id}>
                      <TableCell className="font-medium">#{transaction.id}</TableCell>
                      <TableCell>{transaction.buyer}</TableCell>
                      <TableCell>{transaction.seller}</TableCell>
                      <TableCell>{transaction.item}</TableCell>
                      <TableCell>{transaction.amount}</TableCell>
                      <TableCell>
                        <Badge variant="outline" className="capitalize">
                          {transaction.type}
                        </Badge>
                      </TableCell>
                      <TableCell>
                        <Badge 
                          variant={
                            transaction.status === 'completed' ? 'default' :
                            transaction.status === 'pending' ? 'secondary' : 'destructive'
                          }
                        >
                          {transaction.status}
                        </Badge>
                      </TableCell>
                      <TableCell>{transaction.date}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="reports">
          <Card>
            <CardHeader>
              <CardTitle>Content Moderation</CardTitle>
              <CardDescription>Review and handle reported items</CardDescription>
            </CardHeader>
            <CardContent>
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Item Name</TableHead>
                    <TableHead>Seller</TableHead>
                    <TableHead>Reporter</TableHead>
                    <TableHead>Reason</TableHead>
                    <TableHead>Status</TableHead>
                    <TableHead>Date</TableHead>
                    <TableHead>Actions</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {reportedItems.map((item) => (
                    <TableRow key={item.id}>
                      <TableCell className="font-medium">{item.itemName}</TableCell>
                      <TableCell>{item.seller}</TableCell>
                      <TableCell>{item.reporter}</TableCell>
                      <TableCell>{item.reason}</TableCell>
                      <TableCell>
                        <Badge 
                          variant={
                            item.status === 'resolved' ? 'default' :
                            item.status === 'pending' ? 'secondary' : 'outline'
                          }
                        >
                          {item.status}
                        </Badge>
                      </TableCell>
                      <TableCell>{item.date}</TableCell>
                      <TableCell>
                        {item.status === 'pending' && (
                          <div className="flex space-x-2">
                            <Button 
                              size="sm" 
                              variant="default"
                              onClick={() => handleReportAction(item.id, 'resolve')}
                            >
                              Resolve
                            </Button>
                            <Button 
                              size="sm" 
                              variant="outline"
                              onClick={() => handleReportAction(item.id, 'dismiss')}
                            >
                              Dismiss
                            </Button>
                          </div>
                        )}
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="analytics">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <Card>
              <CardHeader>
                <CardTitle>Platform Statistics</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="flex justify-between">
                  <span>Total Revenue</span>
                  <span className="font-bold">$2,450</span>
                </div>
                <div className="flex justify-between">
                  <span>Items Sold</span>
                  <span className="font-bold">156</span>
                </div>
                <div className="flex justify-between">
                  <span>Items Rented</span>
                  <span className="font-bold">89</span>
                </div>
                <div className="flex justify-between">
                  <span>Average Order Value</span>
                  <span className="font-bold">$78</span>
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>User Activity</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="flex justify-between">
                  <span>Daily Active Users</span>
                  <span className="font-bold">234</span>
                </div>
                <div className="flex justify-between">
                  <span>New Registrations (This Week)</span>
                  <span className="font-bold">12</span>
                </div>
                <div className="flex justify-between">
                  <span>Messages Sent</span>
                  <span className="font-bold">1,456</span>
                </div>
                <div className="flex justify-between">
                  <span>Support Tickets</span>
                  <span className="font-bold">8</span>
                </div>
              </CardContent>
            </Card>
          </div>
        </TabsContent>
      </Tabs>
    </div>
  )
}
